import React from 'react';
import { View, Text, AsyncStorage, Modal, Image, TouchableHighlight } from 'react-native';
import { useRoute } from '@react-navigation/native';
import { TouchableOpacity } from 'react-native-gesture-handler';
import { useState, useEffect } from 'react';
import api from '../../services/api';
import { Feather, AntDesign } from '@expo/vector-icons';



import styles from './styles';


export default function RestaurantMenu() {


    const [modalVisible, setModalVisible] = useState(false);


    const route = useRoute();

    const restaurant = route.params.restaurant;



    return (

        <View style={styles.container}>
            <View style={styles.containerMargin}>
                <View style={styles.contentContainer}>
                    <Image
                        style={styles.profileLogo}
                        source={{ uri: restaurant.logo }}
                    />
                    <Text style={styles.profileName}>{restaurant.name}</Text>
                    <Text style={styles.profileName}>Cardapio</Text>

                    <View style={styles.tagContainer}>

                        <Text style={styles.tag}>
                            tag
                        </Text>
                        <Text style={styles.tag}>
                            tag
                        </Text>
                    </View>
                    <View style={styles.containerButton}>
                        <TouchableOpacity
                            style={styles.followButton}
                        >
                            <Text>Seguir</Text>
                        </TouchableOpacity>
                    </View>
                    <View style={styles.containerButton}>
                        <TouchableOpacity
                            style={styles.followButton}
                            onPress={() => {
                                setModalVisible(true);
                            }}
                        >
                            <AntDesign name="qrcode" size={80} color="white" />
                        </TouchableOpacity>
                    </View>
                    <View style={styles.card}>
                        <Text > nome da novidade</Text>
                        <Text> descrição da novidade</Text>
                    </View>
                    <View style={styles.containerButton}>
                        <TouchableOpacity
                            style={styles.followButton}
                            onPress={() => {
                                setModalVisible(true);
                            }}
                        >
                            <Feather name="box" size={80} color="white" />
                        </TouchableOpacity>
                    </View>
                </View>
            </View>
            <Modal
                animationType="fade"
                transparent={true}
                visible={modalVisible}
                onBackdropPress={() => setModalVisible(!modalVisible)}
                onRequestClose={() => { setModalVisible(!modalVisible); }}
            >
                <View style={styles.cardModal}>
                    <View style={styles.modalView}>
                        <View style={styles.modalHeader}>
                            <TouchableHighlight
                                style={styles.circleButton}
                                activeOpacity={0.6}
                                underlayColor="#DDDDDD"
                                onPress={() => { setModalVisible(!modalVisible); }}>
                                <Feather name="x" size={30} color="black" />
                            </TouchableHighlight>
                            <Text>Mac infarto Feliz</Text>
                            <TouchableHighlight
                                style={styles.circleButton}
                                activeOpacity={0.6}
                                underlayColor="#DDDDDD"
                                onPress={() => { setModalVisible(!modalVisible); }}>
                                <Feather name="check" size={30} color="black" />
                            </TouchableHighlight>
                        </View>

                        <View style={styles.modalHeader}>
                            <Text>Tipo de veneno - </Text>
                            <TouchableHighlight
                                onPress={() => { }}>
                                <Text>Aceitar</Text>
                            </TouchableHighlight>
                        </View>
                        <View></View>
                    </View>
                </View>
            </Modal>
        </View>



    );
}