import React from 'react';
import { useNavigation } from '@react-navigation/native';
import { Feather } from '@expo/vector-icons';
import {
    View,
    Text,
    AsyncStorage,
    Image,
    RefreshControl,
    Alert,
} from 'react-native';

import styles from './styles';
import { TouchableOpacity, FlatList } from 'react-native-gesture-handler';

import api from '../../services/api'

import { useState, useEffect } from 'react';
//import GetPosition from '../../functions/getCurrentPosition'


export default function Nearby() {

    const [restaurant, setRestaurant] = useState([]);


    /////////////////////////////////////////////////////////////////
    async function logOut() {
        console.log('LoginnOFF')

        await AsyncStorage.removeItem('@YoomyStorage:remember_token');
        await AsyncStorage.removeItem('@YoomyPhone:phone_number');
        await AsyncStorage.removeItem('@YoomyStorage:userId');

        Alert.alert('loggedOut')
    }
    ///////////////////////////////////////////////////////////////////

    function wait(timeout) {
        return new Promise(resolve => {
            setTimeout(resolve, timeout);
        })
    }

    //////////////////////////////////////////////////////////////////


    useEffect(() => {
        loadRestaurant();
    }, []);

    async function loadRestaurant() {

        let latitude = await AsyncStorage.getItem('@YoomyLocate:lat');
        let longitude = await AsyncStorage.getItem('@YoomyLocate:lng');

        const response = await api.post('nearby_restaurant', {
            lng: longitude,
            lat: latitude,
            apikey: '9283r9qd0w8dy27^&YH&!^F&*hashak9q99'
        })
        console.log('Load Restaurants')
        setRestaurant(response.data);
    }
    //////////////////////////////////////////////////////////////////////
    const navigation = useNavigation();

    function navigateToMain() {
        navigation.navigate('Main')
    }

    function navigateToProfile(restaurant) {
        navigation.navigate('Profile', { restaurant });
    }

    function navigateToRestaurantMenu(restaurant) {
        navigation.navigate('RestaurantMenu', { restaurant });
    }


    //////////////////////////////////////////////////////////////////////

    const [refreshing, setRefreshing] = React.useState(false)

    const onRefresh = React.useCallback(() => {
        setRefreshing(true)
        getUserPosition()

        wait(1500).then(() => {
            loadRestaurant()
            setRefreshing(false)

        }), [refreshing]
    })


    //////////////////////////////////////////////////////////////



    const [userPosition, setUserPosition] = useState([]);

    async function getUserPosition() {
        navigator.geolocation.getCurrentPosition(
            async ({ coords: { latitude, longitude } }) => {
                setUserPosition({
                    latitude,
                    longitude
                });
                await AsyncStorage.multiSet([
                    ['@YoomyLocate:lat', JSON.stringify(latitude)],
                    ['@YoomyLocate:lng', JSON.stringify(longitude)]
                ]);
                console.log('User position has ben set')
            }, //sucesso
            () => { console.log('erro') }, //erro
            {
                timeout: 2000,
                enableHighAccuracy: true,
                maximumAge: 1000,
            }
        );

    }
    //////////////////////////////////////////////////////////////


    return (
        <View style={styles.container}>
            <View style={styles.topBar}>
                <View>
                    <Text style={styles.topBarTitle}> YOOMY </Text>
                </View>
                <View style={styles.topBarIcons}>
                    <TouchableOpacity
                        style={styles.topBarIcons_Icon}
                        onPress={() => navigateToMain()}
                    >
                        <Feather name="bell" size={30} color="grey" />
                    </TouchableOpacity>
                    <TouchableOpacity
                        style={styles.topBarIcons_Icon}
                        onPress={() => { }}
                    >
                        <Feather name="log-out" size={30} color="grey" />
                    </TouchableOpacity>
                    <TouchableOpacity
                        style={styles.topBarIcons_Icon}
                        onPress={() => getUserPosition()}
                    >
                        <Feather name="settings" size={30} color="grey" />
                    </TouchableOpacity>
                </View>
            </View>
            <View style={styles.content}>
                <Text style={styles.title}>Restaurantes proximos</Text>
                <FlatList
                    data={restaurant}
                    refreshControl={<RefreshControl refreshing={refreshing} onRefresh={onRefresh} />}
                    style={styles.cardsListPrincipais}
                    keyExtractor={restaurant => String(restaurant.id)}
                    renderItem={({ item: restaurant }) => (
                        <View style={styles.cardContainer}>
                            <TouchableOpacity
                                style={styles.card}
                                onPress={() => navigateToRestaurantMenu(restaurant)}
                            >
                                <View style={styles.cardContent}>
                                    <Image
                                        style={styles.cardImage}
                                        source={{ uri: restaurant.logo }}
                                    />
                                    <View style={styles.cardDetails}>
                                        <View style={styles.cardText}>
                                            <Text style={styles.cardTitle}>{restaurant.name}</Text>
                                            <View style={styles.cardTags}>
                                                <Text style={styles.cardTag}>{restaurant.tag_primaria}</Text>
                                                <Text style={styles.cardTag}>{restaurant.tag_secundaria}</Text>
                                            </View>
                                            <View style={styles.cardNota}>
                                                <Feather name="star" size={25} color="white" />
                                                <Text style={styles.cardNota_Nota}>{restaurant.score}</Text>
                                                <Text style={styles.cardNota_Avaliations}>{restaurant.number_ratings} avaliações</Text>
                                            </View>
                                        </View>
                                    </View>
                                </View>
                            </TouchableOpacity>
                        </View>
                    )}
                />
            </View>
        </View>
    );
}