import React, { useState, useEffect } from 'react';
import { View, ActivityIndicator, AsyncStorage } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';



const AppStack = createStackNavigator();

import Login from './pages/login';
import Confirm from './pages/confirm';
import Main from './pages/main';
import Profile from './pages/profile';
import Welcome from './pages/welcome';
import Nearby from './pages/nearby';
import Config from './pages/config';
import RestaurantMenu from './pages/RestaurantMenu';



export default function Routes() {



    const [isLoading, setIsLoading] = React.useState(true);
    const [userToken, setUserToken] = React.useState(null);

    async function veryfyUserLogin() {
        let userId = await AsyncStorage.getItem('@YoomyStorage:userId');
        setUserToken(userId)
    }


    useEffect(() => {
        veryfyUserLogin()
        setTimeout(() => {
            setIsLoading(false);
        }, 1000);
    }, []);

    if (isLoading) {
        return (
            <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
                <ActivityIndicator size="large" />
            </View>
        );
    }

    if (userToken != null) {
        return (
            <NavigationContainer>
                <AppStack.Navigator screenOptions={{ headerShown: false }}>
                    <AppStack.Screen name="Main" component={Main} />
                    <AppStack.Screen name="Profile" component={Profile} />
                    <AppStack.Screen name="Nearby" component={Nearby} />
                    <AppStack.Screen name="Config" component={Config} />
                    <AppStack.Screen name="RestaurantMenu" component={RestaurantMenu} />
                </AppStack.Navigator>
                {/* <Tab.Navigator>
                    <AppStack.Screen name="Main" component={Main} />
                    <AppStack.Screen name="Profile" component={Profile} />
                    <AppStack.Screen name="Nearby" component={Nearby} />
                </Tab.Navigator> */}
            </NavigationContainer>
        );
    } else {
        return (
            <NavigationContainer>
                <AppStack.Navigator screenOptions={{ headerShown: false }}>
                    <AppStack.Screen name="Welcome" component={Welcome} />
                    <AppStack.Screen name="Login" component={Login} />
                    <AppStack.Screen name="Confirm" component={Confirm} />
                    <AppStack.Screen name="Main" component={Main} />
                    <AppStack.Screen name="Profile" component={Profile} />
                    <AppStack.Screen name="Nearby" component={Nearby} />
                </AppStack.Navigator>
            </NavigationContainer>
        );

    }

    return (
        <NavigationContainer>
            <AppStack.Navigator screenOptions={{ headerShown: false }}>
                {/* <AppStack.Screen name="Verify" component={Verify} /> */}
                <AppStack.Screen name="Welcome" component={Welcome} />
                <AppStack.Screen name="Login" component={Login} />
                <AppStack.Screen name="Confirm" component={Confirm} />
                <AppStack.Screen name="Main" component={Main} />
                <AppStack.Screen name="Profile" component={Profile} />
                <AppStack.Screen name="Nearby" component={Nearby} />
            </AppStack.Navigator>
        </NavigationContainer>
    );
}