import React from 'react';
import { View, Text, AsyncStorage, Image, Alert, TextInput, Switch } from 'react-native';
import { useRoute } from '@react-navigation/native';
import { TouchableOpacity } from 'react-native-gesture-handler';
import { useState, useEffect } from 'react';
import api from '../../services/api';
import { Feather, AntDesign } from '@expo/vector-icons';



import styles from './styles';


export default function Config() {

    /////////////////////////////////////////////////////////////////
    async function logOut() {
        console.log('LoginnOFF')

        await AsyncStorage.removeItem('@YoomyStorage:remember_token');
        await AsyncStorage.removeItem('@YoomyPhone:phone_number');
        await AsyncStorage.removeItem('@YoomyStorage:userId');

        Alert.alert('loggedOut')
    }
    ///////////////////////////////////////////////////////////////////

    const [switchEnable, setSwitchEnable] = React.useState(false)


    return (

        <View style={styles.container}>
            <View style={styles.containerMargin}>
                <View style={styles.contentContainer}>
                    <Feather
                        style={styles.pageLogo}
                        name="settings"
                        size={80}
                        color="grey"
                    />

                    <Text style={styles.profileName}>Configurações</Text>
                    <TextInput style={styles.tag} placeholder={'Novo nome de usuario'}></TextInput>

                    <View style={styles.ratingContainer}>
                        <AntDesign name="qrcode" size={24} color="black" />
                        <Text style={styles.score}>tag</Text>
                        <Text> Avaliações</Text>
                    </View>
                    <Text>
                        tag
                    </Text>

                    <View style={styles.card}>
                        <Text > nome da novidade</Text>
                        <Text> descrição da novidade</Text>
                    </View>
                    <View style={styles.containerButton}>
                        <TouchableOpacity
                            style={styles.saveButton}
                            onPress={() => { console.log('save') }}
                        >
                            <Text>Save</Text>
                        </TouchableOpacity>
                        <Switch
                            value={switchEnable}
                            onValueChange={(value) => setSwitchEnable(value)}
                            trackColor={{ true: '#86E2AB' }}
                        />
                        <TouchableOpacity
                            style={styles.logoutButton}
                            onPress={
                                () => Alert.alert(
                                    "Deseja realmete sair ? ",
                                    "",
                                    [
                                        {
                                            text: "Cancelar",
                                            onPress: () => console.log("Canceled"),
                                            style: "cancel"
                                        },
                                        { text: "Sair", onPress: () => logOut() }
                                    ],
                                    { cancelable: true }
                                )
                            }
                        >
                            <Feather name="log-out" size={50} color="white" />
                        </TouchableOpacity>
                    </View>

                </View>
            </View>
        </View>
    );
}